@import '../../styles/breakpoints';

.sb-tabs {
  background-color: var(
    --sb-tabs-color-background,
    var(--sbt-color-background)
  );
  border-bottom: 1px solid var(--sbt-theme-tertiary-color-20);
  display: flex;
  flex-shrink: 0;
  justify-content: center;
}

.sb-tabs__list {
  display: flex;
  justify-content: flex-start;
  margin: 0;
  width: 100%;
  @include window-width-max-small {
    margin: 0;
  }
}
